@tailwind base;
@tailwind components;
@tailwind utilities;

::selection {
    background-color: #273043;
    color: white;
  }

  /* Estilo de la barra lateral */
::-webkit-scrollbar {
    width: 8px;
  }
  
  /* Estilo del "thumb" o el control deslizante en la barra lateral */
  ::-webkit-scrollbar-thumb {
    background-color: #273043;
    border-radius: 5px;
  }
  
  /* Estilo del fondo de la barra lateral */
  ::-webkit-scrollbar-track {
    background-color: #1F2137;
  }

@layer components{
    .margins{
        @apply outline outline-white outline-1 rounded-md
    }
    .flexAllCenter{
        @apply flex justify-center items-center;
    }
    /* Estilo de fuente */
    .txtPrincipal{
        font-family: 'Averia Libre', Helvetica, Arial, sans-serif;    
    }
    .txtSecondary{
        font-family: 'Bungee', cursive;
    }
    .txtThird{
        font-family: 'Roboto', sans-serif;
    }
    /* Tama√±os de fuente */
    .txtSm{
        font-size:  calc(0.5em + 0.5vw);
        line-height: 1.4em;
        letter-spacing: 0.1em;
    }
    .txtMd{
        font-size:  calc(0.5em + 0.7vw);
        line-height: 1.6em;
        
    }
    .txtLg{
        font-size:  calc(0.8em + 0.9vw);
        line-height: 1.2em;
    }
    .txtXl{
        font-size:  calc(1.3em + 1.5vw);
        line-height: 1em;
    }
    .txtXl2{
        font-size:  calc(1.5em + 1.8vw);
        line-height: 1em;
    }

    /* Estilos para BurgueBar */
    .burguerAct{
        @apply w-screen h-9/10 flexAllCenter flex-col overflow-hidden fixed top-[calc(10%)] right-0 transition-all bg-c_DarckBlue
        lg:w-1/3 lg:h-full lg:top-0;
    }
    .burguerDes{
        @apply w-0 h-0       flexAllCenter flex-col overflow-hidden fixed top-[calc(10%)] right-0 transition-all 
        lg:w-1/3 lg:h-full lg:top-0;
    }
}